<template>
	<div class="headerImg-container">
		<div class="hc__imgSelect">
			<div class="imgSelect_title">
				<h3>照片</h3>
				<span v-show="imgSelect" @click="updateImg">修改图片</span>
			</div>
			<div class="imgSelect_show">
				<el-upload
				v-show="!imgSelect"
				ref="upload"
				drag
				:show-file-list="false"
				:limit="1"
				action=""
				:on-change="loadingImg"
				:on-exceed="handleExceed"
				:auto-upload="false"
				>
				<div class="el-upload__tip">
					<p>请选择5MB以内的JPG、PNG格式的照片</p>
					<p>(点此选择或拖动文件至此)</p>
				</div>
				</el-upload>
				<img id="cropImg" v-show="imgSelect"/>
			</div>
			<div class="imgSelect__footer">
				<button class="imgSelect__save" @click="imgSave">保存新头像</button>
			</div>
		</div>
		<div class="hc__imgPreview">
			<h3>预览</h3>
			<div class="previewBoxMax"></div>
			<div class="previewBoxMiddle"></div>
			<div class="previewBoxSmall"></div>
			<img :src="afterimg"  width="200px" height="200px"/>
		</div>
	</div>
</template>

<script>
	import Cropper from 'cropperjs'
	import 'cropperjs/dist/cropper.css'
	import {updateUserHeadImg} from '@/network/user.js'
	export default {
		data () {
			return {
				imgSelect: false,
				myCropper: null,
				afterimg: null
			}
		},
		methods: {
			//up-load
			updateImg() {
				this.imgSelect = false;
				this.myCropper.destroy();
			},
			//判断数量
			handleExceed() {
				this.$message('选中数量不能超过1');
			},
			loadingImg(file){
				this.$refs.upload.clearFiles();
				const isFileType = ['image/jpeg', 'image/png'];
				const sucessFileType = isFileType.find(item => item === file.raw.type);
				const sucessFile5m = file.size / 1024 / 1024 / 5;
				//判断文件是否符合
				let reader = new FileReader();
				if(sucessFileType && sucessFile5m) {
					this.imgSelect = true
					//readAsDataURL方法可以将File对象转化为data:URL格式的字符串（base64编码）
					reader.readAsDataURL(file.raw);
					reader.onload = () =>{
						let dataURL = reader.result;
							//将img的src改为刚上传的文件的转换格式
							document.querySelector('#cropImg').src = dataURL;
							const image = document.getElementById('cropImg');
							//创建cropper实例-----------------------------------------
							this.myCropper = new Cropper(image, {
								viewMode: 1,
								dragMode: 'move',
								initialAspectRatio: 1,
								aspectRatio: 1,
								background: false,
								autoCropArea: 0.6,
								zoomOnWheel: true,
								preview: [ document.querySelector('.previewBoxMax'),
													document.querySelector('.previewBoxMiddle'),
													document.querySelector('.previewBoxSmall')]
							})
					}
					//读取上传文件
				}else {
					this.$message({
						message:'请选择5MB以内的JPG、PNG格式的照片',
						type: 'error'
					})
				}
			},
			imgSave() {
				//base64 -> blob -> file
				if( ! this.imgSelect) {
					this.$message('请选择图片')
				}else {
					let newImg = this.myCropper.getCroppedCanvas({	//base64
						imageSmoothingQuality: 'high'
					}).toDataURL('image/jpeg') 
					var blob = this.getBlob(newImg);		//blob
					// console.log(blob)
					const file = new window.File(			//file
						[blob],
						'jpg',
						{ type: blob.type }
					);							
					// console.log(file)
					let formdata = new FormData();
					formdata.append('file', file);
					updateUserHeadImg(formdata).then(res => {
						this.afterimg = this.$store.state.getURL + res.data
						console.log(this.afterimg)
					})
				}
			},
			//
			getBlob(base64Data) {
					let arr = base64Data.split(','),
							fileType = arr[0].match(/:(.*?);/)[1],
							bstr = atob(arr[1]),
							l = bstr.length,
							u8Arr = new Uint8Array(l);
							
					while (l--) {
							u8Arr[l] = bstr.charCodeAt(l);
					}
					return new Blob([u8Arr], {
							type: fileType
					});
			}
		}
	}
</script>

<style>
	.headerImg-container {
		display: flex;
	}
	.imgSelect_title {
		display: flex;
		justify-content: space-between;
	}
	/* 图片选择 */
	.imgSelect_show {
		width: 250px;
		height: 250px;
		overflow: hidden;
	}
	.el-upload {
		width: 100% !important;
		height: 100% !important;
	}
	.el-upload-dragger {
		width: 100% !important;
		height: 250px;
	}
	.el-upload__tip {
		display: flex;
		margin: 0 !important;
		height: 100%;
		padding: 0 10px;
		align-items: center;
		justify-content: center;
		text-align: center;
		flex-direction: column;
	}
	#cropImg {
		opacity: 0;
		display: none;
	}
	.imgSelect__footer {
		margin-top: 10px;
	}
	.imgSelect__save {
		padding: 4px 15px;
		line-height: var(--line-height);
		outline: none;
		border: 0;
		color: #fff;
		background-color: var(--color-text-change);
	}
	.imgSelect__save:hover {
		background-color: var(--color-btn-focus);
	}
	.imgSelect__save:active {
		background-color: var(--color-btn-pressed);
	}
	
	
	/* 预览 */
	.hc__imgPreview>div {
		border: 1px solid #ebebeb;
		margin-bottom: 20px;
	}
	.previewBoxMax {
		height: 70px;
		width: 70px;
		overflow: hidden;
	}
	.previewBoxMiddle {
		height: 50px;
		width: 50px;
		overflow: hidden;
	}
	.previewBoxSmall {
		height: 20px;
		width: 20px;
		overflow: hidden;
	}
</style>
